.l-type {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-top: 20px;
	margin-bottom: 80px;
	&__wrapper {
		display: grid;
		grid-gap: 10px;
		grid-template-columns: 330px;
		grid-template-rows: auto;
		font-size: 18px; }
	&__section-title {
		text-align: center; }
	&__span {
		color: #111116;
		opacity: 0.8; }
	&__title {
		width: 260px;
		margin: 10px auto;
		font-size: 28px; }
	&__text {
		max-width: 250px; }
	&__section {
		height: 300px;
		padding: 10px 20px;
		background-color: #2c2c34;
		border-radius: 10px;
		position: relative;
		color: #fff;
		overflow: hidden; }
	&__circle {
		position: absolute;
		bottom: 0px;
		right: -40px;
		width: 120px;
		height: 120px;
		border-radius: 50%;
		background-color: #e5c591; }
	&__img {
		position: absolute;
		bottom: 0px;
		right: 0;
		height: 363px;
		width: 250px;
		background-repeat: no-repeat;
		background-size: 100px 120px; }
	&__img--type1 {
		background-image: url(../img/l-type-img1.png);
		background-position: 100% 103%; }
	&__img--type2 {
		background-image: url(../img/l-type-img2.png);
		background-position: 108% 99%; }
	&__img--type3 {
		background-image: url(../img/l-type-img3.png);
		background-position: 115% 99%; } }

@media (min-width: 900px) {
	.l-type {
		margin-top: 40px;
		margin-bottom: 120px;
		&__wrapper {
			grid-gap: 20px;
			grid-template-columns: 50% 50%;
			grid-template-rows: 383px;
			font-size: 22px;
			line-height: 24px; }
		&__section-title {
			text-align: left; }
		&__title {
			width: auto;
			margin: 25px 0;
			font-size: 36px;
			line-height: 36px; }
		&__text {
			max-width: 55%; }
		&__section {
			height: 363px;
			padding: 10px 20px; }
		&__circle {
			bottom: 40px;
			right: -100px;
			width: 270px;
			height: 270px; }
		&__img {
			bottom: -10px;
			right: 0;
			height: 363px;
			width: 250px;
			background-size: auto; }
		&__img--type1 {
			background-position: 100% 67%; }
		&__img--type2 {
			background-position: 110% 55%; }
		&__img--type3 {
			background-position: 90% 65%; } } }
