.srok {
	background-image: url(../img/circle-srok.png), url(../img/srok-bg.png);
	background-repeat: no-repeat;
	background-position: 50% 140%, 70% 50%;
	background-color: #2c2c34;
	color: #fff;
	padding-top: 100px;
	&__wrapper {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		text-align: center; }
	&__text {
		font-size: 18px; }
	&__title {
		margin: 0;
		font-size: 28px;
		line-height: 34px;
		width: 300px; }
	&__block {
		display: flex;
		flex-direction: column;
		text-align: left;
		margin-top: 40px;
		margin-bottom: 120px; }
	&__card {
		position: relative;
		display: flex;
		align-items: center;
		background-color: #fff;
		color: #000;
		margin: 20px 0;
		padding-right: 10px;
		border-radius: 10px 10px 30px 10px; }
	&__card::before {
		content: '';
		position: absolute;
		bottom: -10px;
		left: 35px;
		width: 20px;
		height: 20px;
		background-color: #fff;
		transform: rotate(45deg); }
	&__card::after {
		content: '';
		position: absolute;
		bottom: 0;
		left: 95PX;
		width: 2px;
		height: 100%;
		background-color: #e2e2e2; }
	&__number {
		font-size: 53px;
		padding: 0 20px;
		color: #e8cc9e; }
	&__card p {
		font-size: 18px;
		padding-left: 10px; }
	&__card span {
		font-weight: bold; } }

@media (min-width: 900px) {
	.srok {
		background-position: 20% 140%, 70% 50%;
		overflow: hidden;
		&__wrapper {
			align-items: flex-start;
			text-align: left;
			margin-left: 10%; }
		&__text {
			font-size: 20px; }
		&__title {
			width: auto; }
		&__block {
			margin-bottom: 100px; }
		&__card {}
		&__card:first-child {
			transform: translate(100px, 0); }
		&__card:nth-child(2) {
			transform: translate(480px, -60px); }
		&__card:nth-child(3) {
			transform: translate(10px, -60px); }
		&__card:nth-child(4) {
			transform: translate(460px, -110px); }
		&__card:nth-child(5) {
			transform: translate(-50px, -90px); }
		&__card:nth-child(6) {
			transform: translate(410px, -130px); } } }

@media (min-width: 1100px) {
	.srok {
		&__wrapper {
			margin-left: 20%; }
		&__card:first-child {
			transform: translate(0, 0); }
		&__card:nth-child(2) {
			transform: translate(480px, -90px); }
		&__card:nth-child(3) {
			transform: translate(30px, -60px); }
		&__card:nth-child(4) {
			transform: translate(460px, -90px); }
		&__card:nth-child(5) {
			transform: translate(-40px, -90px); }
		&__card:nth-child(6) {
			transform: translate(430px, -110px); } } }
