.gallery {
	&__wrapper {}
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
	font-size: 18px;
	&__span {
		color: #111116; }
	&__title {
		font-size: 28px; }
	&__gallery {
		display: grid;
		grid-gap: 10px;
		grid-template-columns: 330px;
		grid-template-rows: auto; }
	&__photo {
		display: flex;
		width: 330px;
		height: 356px;
		position: relative; }
	&__img {
		border-radius: 10px;
		flex-grow: 1; }
	&__img-mask {
		position: absolute;
		box-sizing: border-box;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		margin: 0;
		border-radius: 10px;
		color: #fff;
		background-color: rgba(#323232, 0.5);
		display: flex;
		align-items: flex-end;
		cursor: pointer;
		transition: 0.5s; }
	&__img-mask:hover {
		opacity: 0; }
	&__img-text {
		padding-left: 20px; }
	&__text {
		display: flex;
		justify-content: center;
		align-items: center; }
	&__btn {
		position: relative;
		width: 53px;
		height: 53px;
		margin: 0 10px;
		background-color: #e8cc9e;
		border-radius: 50%;
		border: none;
		cursor: pointer; }
	&__btn::before,
	&__btn::after {
		content: '';
		position: absolute;
		top: calc(50% - 0.5px);
		left: calc(50% - 12px);
		width: 24px;
		height: 1px;
		background-color: #fff; }
	&__btn::after {
		top: calc(50% - 12px);
		left: calc(50% - 0.5px);
		width: 1px;
		height: 24px; }
	&__btn-circle {
		position: absolute;
		top: calc(50% - 22px);
		left: calc(50% - 22px);
		width: 44px;
		height: 44px;
		border: 1px solid #fff;
		border-radius: 50%; } }

@media (min-width: 900px) {
	.gallery {
		&__wrapper {
			font-size: 22px; }
		&__span {}
		&__title {
			font-size: 36px; }
		&__gallery {
			width: 100%;
			grid-gap: 20px;
			grid-template-columns: 30% 30% 40%;
			grid-template-rows: 30% 30% 30%;
			margin-top: 70px; }
		&__photo {
			width: auto;
			height: auto; }
		&__photo:nth-child(1) {
			grid-column-start: 1;
			grid-column-end: 3; }
		&__photo:nth-child(2) {
			grid-column: 3;
			grid-row-start: 1;
			grid-row-end: 3; }
		&__photo:nth-child(5) {
			grid-column-start: 1;
			grid-column-end: 3;
			grid-row: 3; }
		&__img {}
		&__btn {
			width: 82px;
			height: 82px; } } }
