.info {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
	&__list {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center; }
	&__list li {
		padding: 0; }
	&__list li {
		margin-right: 0; }
	&__wrap {
		display: flex;
		flex-direction: column;
		text-align: left; }
	&__wrap a {
		color: #a7a6a6;
		font-size: 18px;
		line-height: 40px; }
	&__soglas {
		position: relative; }
	&__soglas::before {
		content: '';
		position: absolute;
		bottom: 5px;
		left: 0;
		width: 95%;
		height: 1px;
		background-color: #a7a6a6; }
	&__logo {
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 260px;
		color: #a7a6a6;
		font-size: 18px;
		line-height: 40px; }
	&__logo p {
		padding-top: 10px; } }

@media (min-width: 1100px) {
	.info {
		flex-direction: row-reverse;
		justify-content: space-between;
		width: 80vw;
		&__list {
			flex-direction: row;
			justify-content: space-between;
			width: 330px; }
		&__list li:nth-child(2) {
			order: 2; }
		&__list li:nth-child(3) {
			order: 1; }
		&__list li:last-child {
			order: 3; } } }
