.title {
	display: flex;
	flex-direction: column;
	align-items: center;
	text-align: center;
	margin-top: 50px;
	font-weight: 500;
	&__slogan {
		color: #e8cb9d;
		font-size: 15px; }
	&__title {
		color: #fff;
		font-size: 26px; }
	&__text {
		width: 270px;
		color: #fff;
		font-size: 18px; }
	&__wrap-sale {
		display: flex;
		flex-direction: column; }
	&__text-sale {
		width: 290px;
		color: #d2d2d1;
		font-size: 15px; }
	&__abautMe {
		display: none; } }

@media (min-width: 900px) {
	.title {
		align-items: flex-start;
		margin-top: 200px;
		&__slogan {
			font-size: 24px; }
		&__title {
			font-size: 60px; }
		&__text {
			width: auto;
			font-size: 24px; }
		&__wrap-sale {
			flex-direction: row-reverse;
			align-items: center; }
		&__text-sale {
			font-size: 18px;
			width: 320px;
			margin-left: 20px; }
		&__abautMe {
			display: block;
			margin-left: -70px;
			margin-top: 300px;
			transform: rotate(90deg);
			color: #a7a6a6; } } }

