.comment {
	margin: 20px 0;
	& input {
    	display: none; }
	&__wrapper {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		text-align: center;
		font-size: 18px; }
	&__text {
		margin: 0;
		color: #111116; }
	&__title {
		margin: 10px 0;
		font-size: 28px;
		line-height: 32px; }
	&__slide {
		display: none;
		flex-direction: column;
		justify-content: center;
		text-align: center;
		text-align: left; }
	&__img-block {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		text-align: center;
		margin: auto; }
	&__img-block img {
		margin: 10px 0; }
	&__block {}
	&__name {
		font-weight: bold; }
	&__info {
		margin-top: -10px; }
	&__comment {
		font-size: 16px; }
	&__toggle-block {
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 90%;
		margin: auto; }
	&__toggle {
		position: relative;
		width: 72px;
		height: 72px;
		background-color: transparent;
		border: 2px solid #dfb46d;
		border-radius: 50%;
		cursor: pointer; }
	&__toggle-prev::before,
	&__toggle-prev::after,
	&__toggle-next::before,
	&__toggle-next::after {
		content: '';
		position: absolute;
		top: calc(50% - 1px);
		left: calc(50% - 10px);
		width: 20px;
		height: 2px;
		background-color: #dfb46d;
		transform-origin: 0 0; }
	&__toggle-prev::before,
	&__toggle-next::before {
		transform: rotate(45deg); }
	&__toggle-prev::after,
	&__toggle-next::after {
		transform: rotate(-45deg); }
	&__toggle-next::before,
	&__toggle-next::after {
		transform-origin: 100% 0; }
	&__toggle:hover {
		background-color: #dfb46d; }
	&__toggle-prev:hover::before,
	&__toggle-prev:hover::after,
	&__toggle-next:hover::before,
	&__toggle-next:hover::after {
		background-color: #fff; }
	&__toggle-block label {
	    display: inline-block;
	    width: 12px;
	    height: 12px;
	    background-color: #bebebe;
	    border-radius: 50%;
	    cursor: pointer; } }

#product-1:checked ~ #slide1,
#product-2:checked ~ #slide2,
#product-3:checked ~ #slide3,
#product-4:checked ~ #slide4 {
    display: flex; }

#product-1:checked ~ .comment__toggle-block label[for="product-1"],
#product-2:checked ~ .comment__toggle-block label[for="product-2"],
#product-3:checked ~ .comment__toggle-block label[for="product-3"],
#product-4:checked ~ .comment__toggle-block label[for="product-4"] {
    background-color: #dfb46d; }

@media (min-width: 900px) {
	.comment {
		margin: 40px 0;
		&__slide {
			flex-direction: row-reverse;
			width: 70%;
			min-width: 700px;
			margin-top: 50px; }
		&__img-block {
			margin-left: auto;
			margin-right: 0; }
		&__block {
			width: 60%;
			padding: 10px 5%;
			border-radius: 20px;
			box-shadow: 0 0 30px 1px #e3dede;
			margin-right: 20px; }
		&__user-block {
			display: flex;
			align-items: center;
			margin-top: 10%; }
		&__name {
			margin: 0; }
		&__info {
			margin: 0 20px; }
		&__comment {}
		&__toggle-block {
			width: 310px;
			margin-top: 30px; } } }
