.footer {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	background-image: url(../img/bg-footer.png);
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	background-color: #2c2c34;
	color: #fff;
	padding: 30px 0; }

@media (min-width: 900px) {
	.footer {
		padding: 60px 0;
		&__wrapper {
			display: flex;
			flex-direction: column;
			width: 90%; } } }
