.site {
	&__btn {
		display: block;
		box-sizing: border-box;
		background-image: linear-gradient( -50deg, rgb(232,205,161) 0%, rgb(223,180,109) 100%);
		width: 327px;
		margin: 15px 0;
		padding: 20px 15px;
		border-radius: 50px;
		color: #846738;
		font-size: 16;
		text-align: center;
		border: none;
		transition: 0.2s; }
	&__btn:hover,
	&__btn:focus {
		box-shadow: 0 0 20px #e8cda1; } }
