.header {
	position: relative;
	background-color: #2c2c34;
	background-image: url(../img/backg1.png),url(../img/backg2.png);
	background-repeat: no-repeat;
	background-position: 0 118%, 100% 50%;
	padding: 15PX 0 145px 0; }

@media (min-width: 900px) {
	.header {
		background-image: url(../img/backg-desktop1.png),url(../img/backg-desktop2.png);
		background-position: 79% 190px, 100% 0;
		padding: 15PX 0 400px 0; } }
