.carcas {
	margin: 50px 0;
	&__wrapper {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center; }
	&__text {
		font-size: 18px;
		color: rgba(#111116, 0.5); }
	&__title {
		font-size: 28px;
		margin: 0; }
	&__wrap-btn {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		margin-top: 20px; }
	&__btn {
		box-sizing: border-box;
		width: 327px;
		margin: 15px 0;
		padding: 20px 15px;
		border-radius: 50px;
		background-color: #fff;
		color: #000;
		font-size: 16;
		text-align: center;
		border: none;
		box-shadow: 0px 3px 16px 0px rgba(0, 0, 0, 0.08);
		transition: 0.2s; }
	&__btn:hover,
	&__btn:focus {
		box-shadow: 0px 3px 16px 0px rgba(0, 0, 0, 0.2); }
	&__btnActive {
		color: #fff;
		background-image: linear-gradient( -50deg, rgb(232,205,161) 0%, rgb(223,180,109) 100%);
		box-shadow: none; }
	&__btnActive:hover,
	&__btnActive:focus {
		box-shadow: 0 0 20px #e8cda1; }
	&__slide {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		font-size: 18px; }
	&__wrap-img {
		display: flex;
		position: relative;
		width: 250px;
		height: 215px;
		margin: 30px 0; }
	&__img {
		position: absolute;
		top: 0;
		left: calc(50% - 125px);
		width: 300px;
		height: 220px;
		margin-top: 20px; }
	&__info {}
	&__info-title {}
	&__info-text {
		font-weight: bold; }
	&__advantages {
		color: #111116; }
	&__suport {
		text-align: center;
		color: #4c4c4f; }
	&__question-call {
		font-weight: bold; }
	&__block-call {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center; }
	&__call-text {} }

@media (min-width: 900px) {
	.carcas {
		margin: 100px 0;
		&__wrapper {
			font-size: 18px;
			align-items: flex-start; }
		&__wrap-btn {
			display: flex;
			flex-direction: row; }
		&__btn {
			width: auto;
			min-width: 180px;
			margin-right: 20px; }
		&__slide {
			flex-direction: row-reverse;
			justify-content: space-between;
			align-items: center;
			width: 100%;
			font-size: 22px; }
		&__wrap-img {
			width: 570px;
			height: 500px;
			margin: 0; }
		&__img {
			top: 0;
			left: 0;
			width: 520px;
			height: 450px;
			margin-top: 100px; }
		&__info {
			width: 570px;
			margin-right: 60px; }
		&__text {
			font-size: 22px; }
		&__title {
			font-size: 36px; }
		&__suport {
			text-align: left; }
		&__question-call {
			font-size: 20px; }
		&__block-call {
			flex-direction: row-reverse; }
		&__call-text {
			width: 327px; } } }
