.contact {
	&__list {
		display: flex;
		flex-direction: column;
		list-style: none;
		padding: 0; }
	&__item {
		display: flex;
		align-items: center;
		font-size: 18px;
		padding-left: 30px;
		margin: 10px 0; }
	&__item a:hover,
	&__item a:focus {
		color: #e8cc9d; }
	&__link {
		color: #fff;
		margin: 0; }
	&__link--grey {
		color: #a7a6a6; }
	&__social {
		margin-right: 5px;
		width: 50px;
		height: 30px;
		background-repeat: no-repeat;
		background-position: center; }
	&__vk {
		background-image: url(../img/vk.png); }
	&__telegram {
		background-image: url(../img/telegram.png); }
	&__whatsapp {
		background-image: url(../img/whatsapp.png); } }

@media (min-width: 800px) {
	.contact {
		&__list {
			flex-direction: row;
			flex-wrap: wrap; }
		&__item {
			margin-right: 55px; } } }
