.zayavka {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
	&__title {
		font-size: 28px;
		line-height: 40px;
		padding: 0 10px; }
	&__text {
		font-size: 20px;
		line-height: 30px;
		padding: 0 40px; }
	&__btn {
		font-size: 20px; } }

@media (min-width: 900px) {
	.zayavka {
		padding: 100px 0;
		&__title {
			width: 610px; } } }
