.advantages {
	width: 100%;
	background-color: #2c2c34;
	height: 800px;
	overflow: hidden;
	color: #fff;
	&__wrapper {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center; }
	&__wrapper input {
		display: none; }
	&__slider {
		width: 100%;
		height: 200px;
		background-image: url(../img/adv-les-wood.png);
		background-repeat: no-repeat;
		background-position: center -600px; }
	&__slider-wrap {
		position: relative;
		height: 160px;
		width: 300px;
		margin: 0 auto; }
	&__toggle {
		position: absolute;
		width: 70px;
		height: 70px;
		border-radius: 50%;
		border: none;
		background-image: url(../img/adv-toggle.png);
		background-position: center;
		background-size: 70px 70px;
		background-repeat: no-repeat; }
	&__slider .toggle--active {
		background-image: url(../img/adv-toggle-active.png); }
	&__slider .toggle--position-left {
		top: 40px;
		left: -20px; }
	&__slider .toggle--position-mid {
		top: 70px;
		left: calc(50% - 45px); }
	&__slider .toggle--position-right {
		top: 40px;
		right: 0; }
	&__block {
		display: none;
		width: 60%;
		min-width: 320px; }
	&__title {
		font-size: 28px; }
	&__text {
		font-size: 18px;
		line-height: 30px;
		padding-right: 10%; }
	&__number {
		position: relative;
		font-size: 71px;
		line-height: 120px; }
	&__number::before {
		content: '';
		position: absolute;
		bottom: -15px;
		left: 5px;
		width: 90px;
		height: 1px;
		background-color: rgba(#fff, 0.5); }
	&__text-img {
		position: relative;
		width: 240px;
		font-size: 18px;
		line-height: 30px;
		padding-left: 80px; }
	&__text-img span {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 60px;
		height: 60px;
		background-image: url(../img/foot.png); } }

#list-1:checked ~ #list1,
#list-2:checked ~ #list2,
#list-3:checked ~ #list3 {
    display: block; }

#list-1:checked ~ .advantages__slider label[for="list-1"],
#list-2:checked ~ .advantages__slider label[for="list-2"],
#list-3:checked ~ .advantages__slider label[for="list-3"] {
	background-image: url(../img/adv-toggle-active.png) {
    background-position: center; }
	background-size: 70px 70px;
	background-repeat: no-repeat; }

@media (min-width: 900px) {
	.advantages {
		padding: 100px 0;
		&__wrapper {
			flex-direction: row;
			justify-content: space-between; }
		&__slider {
			height: 710px;
			width: 710px;
			background-image: url(../img/adv-les-wood.png);
			background-position: 100% 100%; }
		&__slider-wrap {
			width: 600px;
			margin-top: 290px;
			margin-right: -350px;
			transform: rotate(90deg); }
		&__toggle {}
		&__slider .toggle--position-left {
			top: 210px;
			left: -20px; }
		&__slider .toggle--position-mid {
			top: 90px;
			left: calc(50% - 45px); }
		&__slider .toggle--position-right {
			top: 210px;
			right: 0; }
		&__block {
			width: 50%;
			padding-left: 90px;
			padding-right: 40px; }
		&__wrap {
			display: flex;
			justify-content: space-between;
			width: 480px; }
		&__number {
			margin-right: 40px; }
		&__number::before {
			top: 25px;
			left: 100px;
			width: 1px;
			height: 80px; } } }
