.step-work {
	margin: 60px 0;
	&__wrapper {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		text-align: center;
		font-size: 18px; }
	&__text {
		margin: 0; }
	&__title {
		font-size: 28px;
		width: 70%; }
	&__list {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		padding: 0;
		margin-top: 40px;
		list-style: none; }
	&__item {
		width: 80%;
		max-width: 470px;
		margin-bottom: 30px; }
	&__item-number {
		font-size: 130px;
		font-weight: bold;
		color: rgba(#171616, 0.1);
		position: relative; }
	&__icon1::before,
	&__icon2::before,
	&__icon3::before,
	&__icon4::before {
		content: '';
		position: absolute;
		top: 40%;
		left: calc(50% - 37px);
		width: 74px;
		height: 74px;
		background-color: #e8cc9d;
		border-radius: 50%;
		background-image: url(../img/step-work/icon1.png);
		background-repeat: no-repeat;
		background-position: center; }
	&__icon2::before {
		background-image: url(../img/step-work/icon2.png); }
	&__icon3::before {
		background-image: url(../img/step-work/icon3.png); }
	&__icon4::before {
		background-image: url(../img/step-work/icon4.png); } }


@media (min-width: 900px) {
	.step-work {
		&__wrapper {}
		&__list {
			flex-direction: row;
			justify-content: space-between;
			align-items: flex-start;
			margin-top: 70px; }
		&__item {
			width: 20%; } } }
