.nav {
	display: flex;
	justify-content: space-between;
	&__btn-menu {
		position: relative;
		width: 55px;
		height: 55px;
		border: 1px solid #fff;
		border-radius: 50%;
		background-color: transparent;
		cursor: pointer;
		z-index: 100;
		transition: 0.2s; }
	&__btn-menu span,
	&__btn-menu span::before,
	&__btn-menu span::after {
		position: absolute;
		top: calc(50% - 1px);
		left: calc(50% - 9px);
		content: '';
		width: 18px;
		height: 2px;
		background-color: rgba(#fff, 0.5);
		transition: 0.5s; }
	&__btn-menu span::before {
		transform: translateY(-5px); }
	&__btn-menu span::after {
		transform: translateY(5px); }
	&__btn-menu:hover,
	&__btn-menu:focus {
		border-color: #e8cda1; }
	&__btn-menu:hover span,
	&__btn-menu:focus span,
	&__btn-menu:hover span::after,
	&__btn-menu:focus span::after,
	&__btn-menu:hover span::before,
	&__btn-menu:focus span::before {
		background-color: rgba(#fff, 1); }
	&__btnMenuActive span {
		height: 0; }
	&__btnMenuActive span::before {
		transform: rotate(-45deg); }
	&__btnMenuActive span::after {
		transform: rotate(45deg); }
	&__logo {
		height: 55px; }
	&__call {
		display: flex;
		flex-direction: column; }
	&__namber-call {
		display: none; }
	&__btn-call {
		position: relative;
		width: 55px;
		height: 55px;
		border-radius: 50%;
		background-color: #e8cb9d; }
	&__btn-call span {
		display: none; }
	&__btn-call::before {
		content: "";
		position: absolute;
		top: calc(50% - 13.5px);
		left: calc(50% - 13px);
		width: 26px;
		height: 27px;
		background-image: url(../img/phone-call-mobile.png);
		background-repeat: no-repeat;
		background-position: 0 0; }
	&__nav {
		display: none;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		background-color: #2c2c34;
		color: #fff;
		z-index: 50; }
	&__navActive {
		display: block; }
	&__wrapper {
		margin-top: 100px; }
	&__title {
		font-size: 28px; }
	&__title-contacts {
		font-size: 28px; }
	&__list {
		display: flex;
		flex-direction: column;
		list-style: none;
		padding: 0; }
	&__item {
		font-size: 18px;
		display: flex; }
	&__link {
		position: relative;
		margin: 10px 0;
		padding-left: 30px;
		color: #fff;
		transition: 0.2s; }
	&__link::before,
	&__link::after {
		content: '';
		position: absolute;
		transition: 0.2s; }
	&__link:hover,
	&__link:focus {
		color: #e8cc9d; }
	&__link:hover::before,
	&__link:focus::before {
		top: 0;
		left: 0;
		width: 20px;
		height: 20px;
		border: 2px solid #e8cc9d;
		border-radius: 50%; }
	&__link:hover::after,
	&__link:focus::after {
		top: 6.5px;
		left: 7px;
		width: 10px;
		height: 10px;
		background-color: #e8cc9d;
		border-radius: 50%; }
	&__link--grey {
		color: #a7a6a6; } }

@media (min-width: 900px) {
	.nav {
		&__btn-menu {
			order: 2; }
		&__call {
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			align-items: flex-end;
			margin-left: auto;
			margin-right: 30px; }
		&__namber-call {
			display: block;
			color: #fff; }
		&__btn-call {
			width: auto;
			height: auto;
			background-color: transparent;
			color: #a7a6a6;
			order: 1; }
		&__btn-call span {
			display: block; }
		&__btn-call::before {
			left: -35px;
			background-image: url(../img/phone-call.svg.png); }
		&__wrapper {
			margin-bottom: 525px; }
		&__list {
			flex-direction: row;
			flex-wrap: wrap;
			width: 1100px; }
		&__link {
			margin: 20px 0;
			margin-right: 50px; }
		&__title {
			font-size: 60px; }
		&__title-contacts {
			font-size: 36px; } } }
